import types from "prop-types";
import React from "react";

export const InfoBlock = ({ className, data }) => {
  
  return (
    <div className={`${className}__info-block`}>
      {data.map((props, index) => {
      const namesProps=Object.keys(data[index]);
        return (
          <div
            className={`${className}__info-block--item`}
            key={`1${className}${index}`}
          >
          {namesProps.map((name,num)=>{
            return(
              <div
              className={`${className}__info-block--${namesProps[num]}`}
          key={`2${className}${num}`}>{props[`${namesProps[num]}`]}</div>
            )
          })}
          </div>
        );
      })}
    </div>
  );
};

InfoBlock.propTypes = {
  prop: types.string
};

InfoBlock.defaultProps = {};
