import { ReactSVG } from "react-svg";
import types from "prop-types";
import React from "react";

import "./styles.scss";

export const BikeBlock = ({ className, data }) => {
  const { maxWidth, subtitle, title, icon, text } = data;
  return (
    <div
      className={`title-block ${className}__title-block`}
      style={{ maxWidth: `${maxWidth * 1 + 16 * 2}px` }}
    >
      <ReactSVG className={`title-block--icon ${className}__title-block--icon`}>
        {icon}
      </ReactSVG>
      <span className={`title-block--title ${className}__title-block--title`}>
        {title}
      </span>
      <span
        className={`title-block--subtitle ${className}__title-block--subtitle`}
      >
        {subtitle}
      </span>
      <p className={`title-block--text ${className}__title-block--text`}>
        {text}
      </p>
    </div>
  );
};

BikeBlock.propTypes = {
  className: types.string,
  maxWidth: types.string,
  icon: types.object.isRequired,
  title: types.string.isRequired,
  subtitle: types.string,
  description: types.string.isRequired,
};

BikeBlock.defaultProps = {
  maxWidth: "100%",
};
